const mongoose = require('mongoose');
import autoIncrement from 'mongoose-auto-increment';
const {
    ObjectId
} = mongoose.Schema;

const {
    Schema
} = mongoose;
const TransRoom = new Schema({
    title: {
        type: String,
        required: true
    },
    body: {
        type: String,
        required: true
    },

    photo: {
        data: Buffer,
        contenType: String
    },
    language: {
        type: String,
        default: 'en'
    },
    users: [{
        type: ObjectId,
        ref: 'User'
    }],
    classmonitor: {
        type: ObjectId,
        ref: 'User'
    },
    date: {
        type: Date,
        default: Date.now()
    },

});

autoIncrement.initialize(mongoose.connection);
TransRoom.plugin(autoIncrement.plugin, {
    model: 'TransRoom',
    field: 'id',
    startAt: 1,
    incrementBy: 1,
    index: true,
});

const model = mongoose.model('TransRoom', TransRoom);

export default model;